.inferior {
    @include tablet {
        display: grid;
        grid-template-columns:2fr 1fr;
        gap: 2rem;
    }
    .blog {
        h4 {
            text-align: center;
        }
    }
    .testimoniales {
        h4 {
            text-align: center;
        }
    }
}
.blog-pagina {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.entrada{
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        gap: 2rem;
    }
    a{
        h4{
            color: $negro;
            margin: 0;
            &::after{
                content: "";
                display: block;
                height: 0.5rem;
                width: 15rem;
                background-color: $verde;
                margin-top: 1rem;
            }
        }
    }
}
.parrafo-meta {
    span {
        color: $amarillo;
    }
}


.comentario {
    background-color: $verde;
    border-radius: 2rem;
    padding: 2rem;
    font-size: 2.4rem;
    blockquote {
        color: $blanco;
        padding-left: 5rem;
        position: relative;
        &::before{
            content: "";
            display: block;
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            position: absolute;
            width: 6rem;
            height: 8rem;
            left: -2rem;
        }
    }
    p{
        color: $blanco;
        text-align: right;
    }
}