.footer{
    padding: calc($separacion /2) 0;
    background-color: $grisOscuro;
    margin-bottom: 0;
    .contenedor-footer {
        padding-top: calc($separacion /2);
    }

    .copyright{
        color: $blanco;
        text-align: center;
    }
    .navegacion{
        visibility: visible;
        opacity: 1;
        height: auto;
        a{
            &:hover{
                color: $verde;
            }
        }
    }
}