.alerta {
    padding: .5rem;
    text-align: center;
    text-transform: uppercase;
    font-weight: $bold;
    margin: 1rem ;
    color: $blanco;

    &.error{
        background-color: $rojo;
    }
    &.insertado {
        background-color: $verde;
    }
}

.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: $verde;
        th {
            color: $blanco;
            padding: 2rem;
        }
    }
    .td-imagen {
        display: flex;
        justify-content: center;
    }
    
    tbody{
        td{
            text-align: center;
        }
    }
}
.imagen-tabla{
    width: 20rem;
}
