.contenedor-anuncios{
    @include tablet {
        @include grid(3, 2rem)
    }
}
.anuncio {
    margin-bottom: 2rem;
    &:last-child{
        margin-bottom: 0;
    }
    background-color: lighten($gris, 5);
    border: 1px solid darken($gris, 5);
    @include tablet {
        margin-bottom: 0;
    }
    .contenido-anuncio{
        padding: 2rem;
        display: flex;
        flex-direction: column;
        gap: .7rem;
        
    }
    h3, p{
        margin: 0;
        max-height: 12rem;
    }
    h3{
        min-height: 5rem;
        overflow: hidden;
    }
    
}
.precio{
    color: $verde;
    font-weight: $bold;
    font-size: 3rem;
}
.iconos-caracteristicas{
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 0;
    li{
        display: flex;
        flex: 1;
        img{
            flex: 0 0 3rem;
            margin-right: 1rem;
        }
        p{
            font-weight: $black;
        }
    }
    
}