html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(214, 5, 5);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #bd0404;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.modo-oscuro {
  background-color: #262626;
}
.modo-oscuro p {
  color: #FFFFFF;
}
.modo-oscuro a, .modo-oscuro h1, .modo-oscuro h2, .modo-oscuro h3, .modo-oscuro h4, .modo-oscuro blockquote {
  color: #e1e1e1;
}
.modo-oscuro .entrada h4 {
  color: #e1e1e1;
}
.modo-oscuro .anuncio {
  background-color: #333333;
}
.modo-oscuro .contenido-anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.modo-oscuro .contenido-anuncio .precio {
  color: #71B100;
}
.modo-oscuro .iconos-caracteristicas img {
  filter: invert(100%);
}
.modo-oscuro form legend, .modo-oscuro form p {
  color: #FFFFFF;
}
.modo-oscuro form label {
  color: #e1e1e1;
}
.modo-oscuro form input:not([type=submit]), .modo-oscuro form textarea, .modo-oscuro form select {
  background-color: #4d4d4d;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.modo-oscuro .propiedades {
  color: #FFFFFF;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  text-align: left;
  max-width: 60rem;
}
@media (max-width: 768px) {
  .contenido-header h1 {
    text-align: center;
    margin-top: 3rem;
  }
  .contenido-header h1.menu {
    margin-top: 5rem;
  }
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .navegacion a:hover {
  color: #71B100;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: end;
  }
}
.barra .derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra .derecha {
    align-items: flex-end;
  }
}
.barra .derecha .dark-mode {
  margin-top: 1rem;
  cursor: pointer;
  width: 3rem;
  filter: invert(100%);
}
@media (min-width: 768px) {
  .barra .derecha .dark-mode {
    margin-top: 0;
  }
}

.navegacion {
  display: flex;
  align-items: center;
  margin-top: 0;
}
@media (max-width: 767px) {
  .navegacion {
    display: block;
    height: 0;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.4s ease-in-out, height 0.3s linear, opacity 0.5s linear;
  }
  .navegacion.activo {
    visibility: visible;
    display: block;
    height: 14rem;
    opacity: 1;
  }
}
.navegacion a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 1.8rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #71B100;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.boton {
  height: 48px;
  position: relative;
  transition: transform 0.4s;
  width: 48px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    display: none;
  }
}
.boton span {
  background-color: #fff;
  width: 2.5rem;
  display: block;
  height: 3px;
  left: 1.1rem;
  position: absolute;
  top: 50%;
  transition: 0.4s;
}
.boton span:first-child {
  transform: translateY(-6px);
}
.boton span:last-child {
  transform: translateY(6px);
}
.boton.active span:first-child {
  transform: rotate(45deg) translate(0);
}
.boton.active span:nth-child(2) {
  transform: scaleX(0);
}
.boton.active span:last-child {
  transform: rotate(-45deg) translate(0);
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos-nosotros .icono {
  text-align: center;
}
.iconos-nosotros .icono img {
  height: 10rem;
}
.iconos-nosotros .icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  margin-bottom: 2rem;
  background-color: #eeeeee;
  border: 1px solid #d4d4d4;
}
.anuncio:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
}
.anuncio .contenido-anuncio {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
  max-height: 12rem;
}
.anuncio h3 {
  min-height: 5rem;
  overflow: hidden;
}

.precio {
  color: #71B100;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  display: flex;
  flex: 1;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  text-align: center;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 10rem 0;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
}
.imagen-contacto h2 {
  font-weight: 900;
  font-size: 4rem;
}
.imagen-contacto p {
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
.inferior .blog h4 {
  text-align: center;
}
.inferior .testimoniales h4 {
  text-align: center;
}

.blog-pagina {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

@media (min-width: 768px) {
  .entrada {
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 2rem;
  }
}
.entrada a h4 {
  color: #000000;
  margin: 0;
}
.entrada a h4::after {
  content: "";
  display: block;
  height: 0.5rem;
  width: 15rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.parrafo-meta span {
  color: #E08709;
}

.comentario {
  background-color: #71B100;
  border-radius: 2rem;
  padding: 2rem;
  font-size: 2.4rem;
}
.comentario blockquote {
  color: #FFFFFF;
  padding-left: 5rem;
  position: relative;
}
.comentario blockquote::before {
  content: "";
  display: block;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  position: absolute;
  width: 6rem;
  height: 8rem;
  left: -2rem;
}
.comentario p {
  color: #FFFFFF;
  text-align: right;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  padding: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  width: 100%;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  width: auto;
  margin: 0;
}

@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.nosotros-texto blockquote {
  margin: 0;
  font-size: 2.4rem;
  font-weight: 900;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  margin: 1rem;
  color: #FFFFFF;
}
.alerta.error {
  background-color: rgb(214, 5, 5);
}
.alerta.insertado {
  background-color: #71B100;
}

.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
.propiedades thead {
  background-color: #71B100;
}
.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
.propiedades .td-imagen {
  display: flex;
  justify-content: center;
}
.propiedades tbody td {
  text-align: center;
}

.imagen-tabla {
  width: 20rem;
}