.modo-oscuro {
    background-color: darken($grisOscuro, 5);
    p{
        color: $blanco;
    }
    a,h1,h2,h3,h4,blockquote {
        color: $gris;
    }
    
    .entrada {
        h4 {
            color: $gris;
        }
    }
    .anuncio {
        background-color: $grisOscuro;
    }
    .contenido-anuncio {
        background-color: $grisOscuro;
        border: 1px solid $grisOscuro;
        .precio {
            color: $verde;
        }
    }
    .iconos-caracteristicas{
        img {
            filter: invert(100%);
        }
    }
    form {
        legend,p {
            color: $blanco;
        }
        label {
            color: $gris;
        }
        input:not([type="submit"]), textarea, select {
            background-color: lighten($grisOscuro, 10);
            border: 1px solid $grisOscuro;
            color: $blanco;
        }
        
    }
    .propiedades{
        color: $blanco;
    }
}