.barra {
    display: flex;
    flex-direction: column;
    padding-top: 3rem;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: end;

    }
    .derecha {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include tablet {
            align-items: flex-end;   
        }
        .dark-mode{
            margin-top: 1rem;
            cursor: pointer;
            width: 3rem;
            filter: invert(100%);
            @include tablet {
                margin-top: 0;
                
            }
        }
    }
}
.navegacion {
        display: flex;
        align-items: center;
        margin-top: 0;
        @media (max-width: 767px) {
            display: block;
            height: 0;
            visibility: hidden;
            opacity: 0;
            transition: visibility .4s ease-in-out,height .3s linear, opacity .5s linear ;
            &.activo {
                visibility: visible;
                display: block;
                height: 14rem;
                opacity: 1;
            }
        }
    
    a{
        text-decoration: none;
        color: $blanco;
        font-weight: $regular;
        font-size: 1.8rem;
        display: block;
        text-align: center;
        &:hover{
            color: $verde;
        }
        @include tablet {
            margin-right: 2rem;
            &:last-of-type{
                margin-right: 0;
            }
        }
    }
    
}


.boton {
    height: 48px;
    position: relative;
    transition: transform .4s;
    width: 48px;
    cursor: pointer;
    @include tablet {
        display: none;
    }
    span {
        background-color: #fff;
        width: 2.5rem;
        display: block;
        height: 3px;
        left: 1.1rem;
        position: absolute;
        top: 50%;
        transition: .4s;
        &:first-child { transform: translateY(-6px) }
        &:last-child { transform: translateY(6px) }
  }
  &.active span {
    &:first-child { transform: rotate(45deg) translate(0) }
    &:nth-child(2) { transform: scaleX(0) }
    &:last-child { transform: rotate(-45deg) translate(0) }
  }
}
